//userdata

export const getuserdata = (
  callback: (arg0: any, arg1: string) => void,
  token: any
) => {
  instance
    .get(`user/list/`, {
      headers: {
        Authorization: token ? `Token ${token}` : '',
        'Content-Type': 'application/json'
      }
    })
    .then(res => {
      callback(res, 'sucess')
    })
    .catch(err => {
      callback(err, err.response)
      logerror(err, err.response)
    })
}

export const logerror = (err: any, errres: any) => {
  console.log('========>>> error in frontend logged ', err)

  let data = {
    error_title: 'front end error',
    error_message: JSON.stringify(errres),
    additional_info: JSON.stringify(err)
  }
  addusererrorlog(data)
}
