import { Store, useStore } from './Store'
import getuser from '../utils/api/getuser'
import edituser from '../utils/api/edituser'
import getcurrentuser from '../utils/api/getcurrentuser'
import editprofileuser from '../utils/api/editprofileuser'
import { logerror } from 'utils/actions'
export class userItem {
  id: String
  image: String
  firstname: String
  lastname: String
  email: String
  address: String
  dob: String
  user_type: String
  gender: String
  auth_type: String
  photo_url: String
  dial_code: String
  phone: String
  is_deleted: String
  updated_by: String
  owned_by: String
  created_at: String
  updated_at: String
  created_by: String
  TextData: any

  constructor(o: {
    id: String
    image: String
    firstname: String
    lastname: String
    email: String
    address: String
    dob: String
    user_type: String
    gender: String
    auth_type: String
    photo_url: String
    dial_code: String
    phone: String
    is_deleted: String
    updated_by: String
    owned_by: String
    created_at: String
    updated_at: String
    created_by: String
    TextData: any
  }) {
    this.id = o.id
    this.image = o.image
    this.firstname = o.firstname
    this.lastname = o.lastname
    this.email = o.email
    this.address = o.address
    this.dob = o.dob
    this.user_type = o.user_type
    this.gender = o.gender
    this.auth_type = o.auth_type
    this.photo_url = o.photo_url
    this.dial_code = o.dial_code
    this.phone = o.phone
    this.is_deleted = o.is_deleted
    this.updated_by = o.updated_by
    this.owned_by = o.owned_by
    this.created_at = o.created_at
    this.updated_at = o.updated_at
    this.created_by = o.created_by
    this.TextData = o.TextData
  }
}

export const userStore = new Store<userItem[] | false>(false)
export const userDetail = new Store<userItem | string>(
  typeof localStorage === 'undefined'
    ? false
    : localStorage.getItem('UserDetails')
    ? JSON.parse(String(localStorage.getItem('UserDetails')))
    : false
)

export const userstore = () => {
  const [user, setuser] = useStore(userStore)
  const [selfuser, setselfuser] = useStore(userDetail)

  return {
    user,
    setuser,
    selfuser,
    setselfuser,

    async loaduser() {
      await getuser()
        .then(data => {
          setuser(data.data)
          console.log('selfuser selfuser selfuser :', data.data)
        })
        .catch(err => {
          console.log(err)
          logerror(err, err.response)
        })
    },
    async edituser(id: any, data: any) {
      await edituser(id, data)
        .then(res => {
          setuser(oldarr => {
            return (
              oldarr &&
              oldarr.map((obj: any) => {
                const getobj = (o: any) => {
                  let x = o
                  x.image = data.image
                  x.firstname = data.firstname
                  x.lastname = data.lastname
                  x.email = data.email
                  x.address = data.address
                  x.dob = data.dob
                  x.user_type = data.user_type
                  x.gender = data.gender
                  x.auth_type = data.auth_type
                  x.photo_url = data.photo_url
                  x.dial_code = data.dial_code
                  x.phone = data.phone
                  return x
                }
                return String(obj.id) === String(id) ? getobj(obj) : obj
              })
            )
          })
        })
        .catch(err => {
          console.log(err)
          logerror(err, err.response)
        })
    },

    async loadcurrentuser() {
      await getcurrentuser()
        .then((data: any) => {
          console.log('currentuser :', data.data.result)
          setselfuser(data.data.result)
        })
        .catch(err => {
          console.log(err)
          logerror(err, err.response)
        })
    },
    async editprofileuser(data: any) {
      await editprofileuser(data)
        .then(res => {
          console.log('edit >>> :', data, res)

          setselfuser((oldarr: any) => {
            let x = oldarr
            x.firstname = data.firstname
            x.lastname = data.lastname
            x.email = data.email
            x.image = data.image
            x.photo_url = data.photo_url
            x.dob = data.dob
            x.gender = data.gender
            x.address = data.address
            x.dial_code = data.dial_code
            x.phone = data.phone

            localStorage.setItem('UserDetails', JSON.stringify(x))

            return x
          })
        })
        .catch(err => {
          console.log(err)
          logerror(err, err.response)
        })
    }
  }
}
